
import { defineComponent } from "vue"
import { PAGE_SIZE } from '@/shared/constants/common.const'
import { BillerInquiryList, PAY20700111_REQ, PAY20700111_RES } from "@/shared/type/PAY20700111.type"
import Pagination from '@/shared/components/Pagination/Pagination.vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue'
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";

export default defineComponent({
    name: "PAY207001SC00",

    components: {
        Pagination,
        BreadCrumb,
        SelectOption
    },

    data() {
        return {
            header: [
                { columnName: this.$t('pay207001sc00.column.no') },
                { columnName: this.$t('pay207001sc00.column.edit') },
                { columnName: this.$t('pay207001sc00.column.biller_no') },
                { columnName: this.$t('pay207001sc00.column.biller_code') },
                { columnName: this.$t('pay207001sc00.column.biller_name') },
                { columnName: this.$t('pay207001sc00.column.biller_category') },
                { columnName: this.$t('pay207001sc00.column.biller_mcc_code') },
                { columnName: this.$t('pay207001sc00.column.aggregator_type') },
                { columnName: this.$t('pay207001sc00.column.api_access_type') },
                { columnName: this.$t('pay207001sc00.column.biller_preCheck_type') },
                { columnName: this.$t('pay207001sc00.column.payment_by_type') },
                { columnName: this.$t('pay207001sc00.column.status') },
            ],
            searchForm: {
                billerCode: "",
                billerNo: '',
                billerName: "",
                billerCategoryCode: "",
                billerStatusCode: "",
                aggregatorTypeCode: "",
            },
            billerList: [] as BillerInquiryList[],
            totalItems: 0 as number,
            currentPage: 1 as number,
            pageSize: PAGE_SIZE as number,
            startingIndex: 1 as number,
            isReset: false as boolean
        }
    },

    created() {
        this.requestBillerList()
    },

    computed: {
        billerCategoryCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.BillerCategoryCode
        },

        aggregatorTypeCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.AggregatorTypeCode
        },

        billerStatusCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.BillerStatusCode
        }
    },

    methods: {
        async requestBillerList() {
            const reqBody = {
                billerCode: this.searchForm.billerCode,
                billerNo: this.searchForm.billerNo,
                billerName: this.searchForm.billerName,
                billerCategoryCode: this.searchForm.billerCategoryCode,
                aggregatorTypeCode: this.searchForm.aggregatorTypeCode,
                billerStatusCode: this.searchForm.billerStatusCode,
                pageNumber: this.currentPage,
                pageSize: this.pageSize
            } as PAY20700111_REQ
            const res = await this.$request.getRequest("PAY20700111", reqBody, false) as PAY20700111_RES
            if (res?.header.result) {
                this.billerList = res.body.list.map((item: BillerInquiryList) => {
                    return {
                        billerNo: `${item.billerNo}`,
                        billerCode: `${item.billerCode}`,
                        billerName: `${item.billerName}`,
                        billerCategoryCode: `${item.billerCategoryCode} : ${this.$codeConverter.convertValue("BillerCategoryCode", item.billerCategoryCode)}`,
                        billerMCC: `${item.billerMCC} : ${this.$codeConverter.convertValue("BillerMCC", item.billerMCC)}`,
                        aggregatorTypeCode: `${item.aggregatorTypeCode} : ${this.$codeConverter.convertValue("AggregatorTypeCode", item.aggregatorTypeCode)}`,
                        apiAccessTypeCode: `${item.apiAccessTypeCode} : ${this.$codeConverter.convertValue("APIAccessTypeCode", item.apiAccessTypeCode)}`,
                        billerPreCheckTypeCode: `${item.billerPreCheckTypeCode} : ${this.$codeConverter.convertValue("BillerPreCheckTypeCode", item.billerPreCheckTypeCode)}`,
                        paymentByTypeCode: `${item.paymentByTypeCode} : ${this.$codeConverter.convertValue("PaymentByTypeCode", item.paymentByTypeCode)}`,
                        billerStatusCode: `${item.billerStatusCode} : ${this.$codeConverter.convertValue("BillerStatusCode", item.billerStatusCode)}`
                    }
                })
                this.totalItems = res.body.totalCount
            }
        },

        statusConverter(statusCode: string): string {
            switch (statusCode) {
                case '01 : Registered':
                    return 'info'
                case '02 : Cancel':
                    return 'warning'
                case '03 : Normal':
                    return 'success'
                case '04 : Termination':
                    return 'fail'
                case '05 : Request Approval':
                    return 'info'
                default:
                    return 'info'
            }
        },

        handleClickAction(actionType: string) {
            switch (actionType) {
                case 'search':
                    if (this.currentPage > 1) {
                        this.currentPage = 1
                        this.isReset = true
                        this.requestBillerList()
                    } else {
                        this.requestBillerList()
                    }
                    break;
                case 'register':
                    this.$router.push({
                        path: `PAY207001SC01`,
                        query: {
                            type: "register"
                        }
                    })
                    break;
                default: //case reset
                    this.currentPage = 1
                    this.isReset = true;
                    this.searchForm.billerCode = ''
                    this.searchForm.billerName = ''
                    this.searchForm.billerNo = ''
                    this.searchForm.billerCategoryCode = ''
                    this.searchForm.aggregatorTypeCode = ''
                    this.searchForm.billerStatusCode = ''
                    this.requestBillerList()
                    break;
            }
        },

        handleRowClick(item) {
            this.$router.push({
                path: `PAY207001SC01`,
                query: {
                    type: "edit",
                    billerNo: item.billerNo,
                    billerCode: item.billerCode
                }
            })
        },

        handlePagination(pageNumber: number) {
            this.isReset = false
            this.currentPage = pageNumber
            this.requestBillerList()
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex
        }
    }
});
